import React from 'react';

const SearchBox = ({searchfield, searchChange}) => {
    return (
        <div className='pa2'>
            <input 
                className='tc pa2 ma2 ba b--green bg-lightest-blue'
                type='search' 
                placeholder='Search for Friends :D' 
                onChange={searchChange}
            />
        </div>
    );
}

export default SearchBox;